nav {
  display: flex;
  background-color: rgb(0, 3, 48);
  height: 4rem;
  justify-content: center;
  align-items: center;
}

li {
  list-style: none;
  margin: 0 2rem 0 2rem;
}

button {
  border: none;
  background-color: aqua;
  color: rgb(0, 3, 48);
  text-transform: uppercase;
  width: 4rem;
  height: 2rem;
  transition: 0.1s;
}

button:hover {
  background-color: white;
  cursor: pointer;
}

/* /////////////////////////BLOG FORM */

.BlogForm {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.BlogInputs {
  display: flex;
  flex-direction: column;
  margin: 5rem 0 0 0;
}

.post-button {
  border: none;
  background-color: rgb(0, 5, 77);
  color: white;
  margin: 1rem 0 0 0;
}

.post-button:hover {
  background-color: rgb(0, 19, 129);
}

/* ///////////////////////////Contact Form */

.contact-form-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.contact-form {
  display: flex;
  flex-direction: column;
  margin: 3rem 0 0 0;
}

.contact-label {
  margin: 1rem 0 0 0;
}

.create-contact {
  background-color: rgb(0, 5, 77);
  color: white;
  margin: 1rem 0 0 0;
  height: auto;
  width: 6rem;
}

.create-contact:hover {
  background-color: rgb(0, 19, 129);
}

.contact-list {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  background-color: rgb(0, 5, 77);
  padding: 0px;
  width: 60rem;
}

.list-item {
  color: white;
  text-transform: uppercase;
}

/* ///////////////////////////READING APP */

.reading-app-container {
  display: flex;
}

.blog-list {
  display: flex;
  flex-direction: column;
  margin: 0 0 0 0;
  padding: 0rem;
}

.blog-list li {
  list-style: none;
}

.blog-list li h1:hover {
  cursor: pointer;
}
